	module.exports = {"data":{"site":{"siteMetadata":{"title":"Shang-Yu Su","subtitle":"PhD student at National Taiwan University, research interests cover Deep Learning, Natural Language Processing, and dialogue systems.","copyright":"© All rights reserved.","author":{"name":"Shang-Yu Su","twitter":"#"},"disqusShortname":"","url":"https://www.shangyusu.com"}},"markdownRemark":{"id":"/Users/ShangYu/Desktop/github/shangyusu.github.io/src/pages/articles/AAAI2019-DSTC7-track2/index.md absPath of file >>> MarkdownRemark","html":"<p><b>This paper is published in the 7th Dialog System Technology Challenge (DSTC7) in the Proceedings of Thirty-Third AAAI Conference on Artificial Intelligence (AAAI 2019 - DSTC7).</b></p>\n<p><b>NOTE: Also published in Computer Speech and Language (Journal, 2020)</b></p>\n<p>Full paper:\n<a href=\"/AAAI_2019___DSTC_Track_2-1ac5ec1418e3e3049e3e156b0d215230.pdf\" target=\"_blank\">Here</a></p>\n<p>End-to-end dialogue generation has achieved promising results without using handcrafted features and attributes specific for each task and corpus. However, one of the fatal drawbacks in such approaches is that they are unable to generate informative utterances, so it limits their usage from some real-world conversational applications. This paper attempts at generating diverse and informative responses with a variational generation model, which contains a joint attention mechanism conditioning on the information from both dialogue contexts and extra knowledge.</p>","fields":{"tagSlugs":["/tags/dialogue/","/tags/response-generation/"]},"frontmatter":{"title":"Knowledge-Grounded Response Generation with Deep Attentional Latent-Variable Model","tags":["Dialogue","Response Generation"],"date":"2018-12-6T22:40:32.169Z","description":"Hao-Tong Ye, Kai-Ling Lo, Shang-Yu Su, and Yun-Nung Chen","conference":"DSTC7/Computer Speech and Language 2020"}}},"pathContext":{"slug":"/posts/aaai2019-dstc7-cvae/"}}

